import React, { Component } from 'react';
import {Menu, Button, message, Icon, Tooltip, Popconfirm} from 'antd';
import TdExcel from './td-excel';
import Handsontable from 'handsontable';
import {allControlles} from './Controlles';
import $ from 'jquery';
import "jquery/dist/jquery.js";
import "jquery-ui/ui/widgets/draggable.js";
import "jquery-ui/ui/widgets/droppable.js";
import "jquery-contextmenu/dist/jquery.contextMenu.min.js";
import "jquery-contextmenu/dist/jquery.ui.position.min";
import {LoadControlProps} from "./components";

class Main extends Component {
		constructor(props) {
				super(props);
				this.excelRef = React.createRef();
				this.state = {
						saving: false,

						curCell: null,
						curCellDef: null,
						curSubField: null,
						curSubFieldIndex: 0
				}
				this.prevExcel = null;
				this.bindExcelEvent.bind(this);
				this.nowActiveKey = this.changingActiveKey = '';
				this.saveFormTimer = null;
		}

		goto(item) {
				let {formId} = this.props;
				let {flowId, flowType} = this.props.formDetail;
				let url;

				switch(item.key){
						case "flow":
								url = (parseInt(flowType) === 1 ? '/general/system/approve_center/flow_guide/flow_type/flow_design/index.php?FLOW_ID=' : '/general/system/approve_center/flow_guide/flow_type/set_new.php?FLOW_ID=') + flowId;
								break;
						case "trigger":
								url = "/general/appbuilder/web/appdesign/appformulas?formId=" + formId;
								break;
						case "priv":
								url = "/general/appbuilder/web/appdesign/apppriv?formId=" + formId;
								break;
						case "print":
								url = "/general/appbuilder/web/appdesign/appprint?formId=" + formId + "&printId=";
								break;
						case "task":
								url = "/general/appbuilder/web/appdesign/apptimer?formId=" + formId;
								break;
						case "subForm":
								url = "/general/appbuilder/web/appdesign/appsubform?formId=" + formId;
								break;
						case "preview":
								url = "/general/appbuilder/web/appcenter/appdata/view?formId=" + formId + "&type=preview";
								break;
						case "custom":
								url = "/general/appbuilder/web/appdesign/appcustom?formId=" + formId;
								break;
						default:
								return;
				}

				if(item.key == 'flow'){
            if(parseInt(flowId) <= 0)
            {
//            		if(window.confirm('是否要启用流程？')){
//            				$.get('/general/appbuilder/web/appdesign/appform/usingbpm', {formId}, (res) => {
//			                 if(res.status === 'ok') {
//			                      this.props.setFlowId(res.data);
//			                      window.open(`${url.slice(0, url.length-1)}${res.data}`);
//	                     } else {
//	                      	message.error(res.message);
//	                     }
//                  })
//            		}
            } else {
              	window.open(url, "_BLANK");
            }
        } else {
            window.open(url, "_BLANK");
        }
		}

		usingBpm(flowType){
				let {formId} = this.props;
				$.get('/general/appbuilder/web/appdesign/appform/usingbpm', {formId, flowType}, (res) => {
           if(res.status === 'ok') {
                this.props.setFlowId(res.data, res.flowType);
                let url = '';
                if(parseInt(res.flowType) === 1){
                	url = '/general/system/approve_center/flow_guide/flow_type/flow_design/index.php?FLOW_ID=' + res.data;
                }else{
                	url = '/general/system/approve_center/flow_guide/flow_type/set_new.php?FLOW_ID=' + res.data;
                }
                window.open(url);
           } else {
            	message.error(res.message);
           }
        });
		}

		saveForm(){
				if(this.state.saving){
						return;
				}
				this.setState({saving: true});
				let me = this;
				if(this.props.importExcel){
					let b_reload = false;
					this.props.formDetail.formData.map(sheet => {
						if(!b_reload && sheet.index != me.props.activeKey && !sheet.cellStyles){
								b_reload = true;
								me.excelRef.current.changeSheet(sheet.index);
								return;
						}
					});
					if(!b_reload){
							this.props.setImportExcel(false);
							if(this.nowActiveKey != this.props.activeKey){
									this.excelRef.current.changeSheet(this.nowActiveKey);
							}
					}
					return;
				}
				let sheets = this.excelRef.current.changeSheet(-1);
				window.setTimeout(() => {
						let {formId, formDetail} = me.props;
						let {isStop} = formDetail;
						me.props.saveForm(formId, isStop, sheets, ()=>me.setState({saving: false}));
				}, 0);
		}

    componentWillReceiveProps(nextProps){
				this.excelRef.current.importExcel = true;
				this.setState({curCell: null, curCellDef: null, curSubField: null, curSubFieldIndex: 0});
    }

    componentDidUpdate(prevProps, prevState) {
    		if(this.excelRef.current && this.excelRef.current.excel){
    				this.initDrop();
    		}
				if(this.nowActiveKey != '' && this.changingActiveKey != this.props.activeKey){
					if(this.props.importExcel){
					}else{
						this.nowActiveKey = '';
					}
					this.setState({saving: false});
					if(this.saveFormTimer){
						window.clearTimeout(this.saveFormTimer);
					}
					this.saveFormTimer = window.setTimeout(()=>this.saveForm(), 0);
					this.changingActiveKey = this.props.activeKey;
				}
    }

		render(){
				let {frmno, name, flowId, isStop, codes, flowType} = this.props.formDetail;
				let sheets = this.props.formDetail.formData ? this.props.formDetail.formData : null;
				if(!sheets){
					sheets = [{index: "1", name: '工作表1', cellSettings: {}, cellMetas: {}, charts: [], images: [], cellStyles: [], fieldMaps: []}];
				}
				let {formId, formsOfApp, columnsOfForm, activeKey} = this.props;

				return (
						<div className="main">
								<div className="header">
										<Menu mode="horizontal" selectedKeys={['form']} defaultSelectedKeys={['form']} style={{'backgroundColor': 'transparent', 'color': '#fff'}} onClick={this.goto.bind(this)}>
												<span className="td-app-name">{'['}{frmno}{']'}{name}</span>
												<Menu.Item key='form'>表单设计</Menu.Item>
												<Menu.Item key='flow'>
												{
													parseInt(flowId)<=0 ?
													<Popconfirm
												    title="请点击需要启用的流程类型"
												    icon={<Icon type="question-circle-o" style={{ color: 'green' }} />}
												    onConfirm={this.usingBpm.bind(this, 1)}
    												onCancel={this.usingBpm.bind(this, 2)}
												    okText="固定流程"
												    cancelText="自由流程"
												  >流程设置</Popconfirm> : (parseInt(flowType)===1 ? '固定流程' : '自由流程')
												}
  											</Menu.Item>
												<Menu.Item key='trigger'>触发器</Menu.Item>
												<Menu.Item key='priv'>权限</Menu.Item>
												<Menu.Item key='print'>打印模板</Menu.Item>
												<Menu.Item key='task'>定时任务</Menu.Item>
												<Menu.Item key='subForm'>子表单</Menu.Item>
												<Menu.Item key='custom'>自定义操作</Menu.Item>
												<Menu.Item key='preview'>预览</Menu.Item>
												{
														parseInt(flowId) > 0 ?
																<a href="javascript:void(0);" className="td-app-name-isstop">
												            <label className="ant-checkbox-wrapper">
												                <input type="checkbox" checked={isStop} onChange={e => this.props.setIsStop(e.target.checked)}/>
												                <span>是否停用流程</span>
												            </label>
												        </a> : null
												}
												<span style={{"float":"right"}}>
														<Button type="primary" onClick={() => {if(this.props.importExcel){this.changingActiveKey = ''; this.nowActiveKey = this.props.activeKey;} this.saveForm();}} loading={this.state.saving} disabled={false}>{this.state.saving ? "保存中" : "保存"}</Button>
												</span>
										</Menu>
								</div>
								<div className="main-content">
										<div className="sidebar">
												<Menu>
												{
														allControlles.map(control => <Menu.Item key={control.name} dataType={control.name} dataName={control.short}><Tooltip title={"请把 " + control.label + " 拖拽到中部的单元格中，然后点击该单元格并在右部定义其相关属性。"}><Icon component={control.icon} style={{ color: 'green', fontSize: '22px' }}/>{control.label}</Tooltip></Menu.Item>)
												}
												</Menu>
										</div>
										<div className="config-bar">
												{
														this.state.curCellDef ? LoadControlProps(this.state.curSubField ? this.state.curSubField.type : this.state.curCellDef.type, formId, formsOfApp, columnsOfForm, this, this.state.curSubField ? this.state.curSubField : this.state.curCellDef, this.state.curSubField && this.state.curCellDef ? this.state.curCellDef.name : '', codes) : null
												}
										</div>
										<div className="layout">
										{
												<TdExcel ref={this.excelRef} single={false} owner={this} name={name} sheets={sheets} activeKey={activeKey} items={columnsOfForm} setForm={this.props.setFormDetailData} setActiveKey={this.props.setActiveKey} setImportExcel={this.props.setImportExcel}/>
										}
										</div>
								</div>
						</div>
				)
		}

		//初始化控件推拽
		initDraggable(){
				$('div.sidebar li').draggable({
						appendTo: '#td_sheets' + this.excelRef.current.id,
						revert: "invalid",
						scroll: false,
						refreshPositions: false,
						helper: "clone"
				});
		}

		 //初始化拖拽放置
    initDrop(){
        let me = this
        let excel = this.excelRef.current.excel;

       	$('#td_sheets' + this.excelRef.current.id + ' i.anticon').draggable({
						appendTo: '#td_sheets' + this.excelRef.current.id,// + " td",
						revert: "invalid",
						scroll: false,
						refreshPositions: false,
						helper: "clone"
				});

        $("#td_sheets"  + this.excelRef.current.id + " td").droppable({
            drop:function(event,ui){
            		let pos = excel.getCoords(event.target);
								//excel.setDataAtCell(pos.row, pos.col, "[" + $(ui.helper).attr('dataName') + "]");
								if($(ui.helper).attr('dataType')){
										let meta = excel.getCellMeta(pos.row, pos.col);
										if(!meta.cellDef){
												meta.cellDef = {};
										}
										meta.cellDef['type'] = $(ui.helper).attr('dataType');
										meta.cellDef['title'] = $(ui.helper).attr('dataName');
										meta.cellDef['name'] = meta.cellDef['title'];
										if(['attachment', 'image', 'user-select', 'dept-select'].indexOf(meta.cellDef['type']) != -1){
											meta.cellDef['multi'] = true;
										}
										meta["readOnly"] = true;
										excel.setCellMetaObject(pos.row, pos.col, meta);
										excel.render();
								}else if($(ui.helper).attr('row')){
									 let row = $(ui.helper).attr('row'), col = $(ui.helper).attr('col');
									 if(pos.row != row || pos.col != col){
									 		let old_meta = excel.getCellMeta(row, col);
									 		if(old_meta['cellDef']){
											 		let meta = excel.getCellMeta(pos.row, pos.col);
											 		meta['cellDef'] = old_meta['cellDef'];
											 		delete old_meta['cellDef'];
													excel.setCellMetaObject(pos.row, pos.col, meta);
													excel.setCellMetaObject(row, col, old_meta);
													excel.render();
											}
									 }
								}
            }
        });
    }

    sysCurCellMeta(def){
    		if(this.state.curCellDef && this.state.curCellDef["type"] === "list" && !(def["type"] === "list" || this.state.curSubField)){
    				return;
    		}
    		if(!this.state.curCellDef){
    				return;
    		}
    		let me = this;
    		let b_loadsheet = true;
    		let excel = this.excelRef.current.excel;
    		if(this.state.curSubField){
    			  if(this.state.curSubField["type"] != def["type"]){//修改类型时确保重新保存控件以刷新相关属性
    			  	def['ischanged'] = true;
    				}
    				this.state.curSubField = def;
    				this.state.curCellDef["subFields"][this.state.curSubFieldIndex] = this.state.curSubField;
    				def = this.state.curCellDef;
    		}else{
    				if(this.state.curCellDef["type"] != def["type"]){//修改类型时确保重新保存控件以刷新相关属性
    					def['ischanged'] = true;
    				}
    		}
    		this.state.curCell.meta['cellDef'] = def;
    		excel.setCellMetaObject(this.state.curCell.row, this.state.curCell.column, this.state.curCell.meta);
    		this.setState({curCell: {row: this.state.curCell.row, column: this.state.curCell.column, meta: this.state.curCell.meta}, curCellDef: def, curSubField: this.state.curSubField});
//    		if(b_loadsheet && !b_change_type){
//    				window.setTimeout(() => {
//    					let excel = me.excelRef.current.excel;
//    					excel.render();
//    					if(me.state.curCell){
//              		excel.selectCell(me.state.curCell.row, me.state.curCell.column, me.state.curCell.row, me.state.curCell.column);
//            	}
//    				}, 0);
//    		}
    }

    componentDidMount() {
    		this.initDraggable();
    		this.componentDidUpdate();//绑定事件
		}

		bindExcelEvent(excel){
				let me = this;
	  		this.prevExcel = excel;
				let changeControlProps = function(row, column, row2, column2, selectionLayerLevel) {
						if(me.excelRef.current.b_skip_after_selecion_end){
								me.excelRef.current.changeSheet(-1);
								me.excelRef.current.b_skip_after_selecion_end = false;
								return;
						}
						if(!(me.state.curCell && me.state.curCell.row == row && me.state.curCell.column == column)){
								let meta = this.getCellMeta(row, column);
								me.setState({curCell: {row, column, meta}, curCellDef: meta.cellDef, curSubField: null, curSubFieldIndex: 0});
								if(!meta.cellDef){
										me.comfirmControlProp = null;
								}
						}
				}
				let doComfirmControlProp = function(){
						if(me.comfirmControlProp){//更新上一个单元格设置
								me.comfirmControlProp();
								me.comfirmControlProp = null;
						}
				}
				Handsontable.hooks.add('afterSelectionEnd', changeControlProps.bind(excel), excel);
				//Handsontable.hooks.add('afterRender', me.initDrop.bind(this));
				Handsontable.hooks.add('afterOnCellMouseDown', () => doComfirmControlProp());
				Handsontable.hooks.add('beforeOnCellMouseDown', () => doComfirmControlProp());
				Handsontable.hooks.add('beforeRowResize', (currentRow, newSize, isDoubleClick) => {
					doComfirmControlProp();
					me.excelRef.current.b_skip_after_selecion_end=true;
				});
				Handsontable.hooks.add('beforeColumnResize', (currentColumn, newSize, isDoubleClick) => {
					doComfirmControlProp();
					me.excelRef.current.b_skip_after_selecion_end = true;
				});
//				Handsontable.hooks.add('afterRowResize', (currentRow, newSize, isDoubleClick) => {
//					me.excelRef.current.b_skip_after_selecion_end = false;
//				});
//				Handsontable.hooks.add('afterColumnResize', (currentColumn, newSize, isDoubleClick) => {
//					me.excelRef.current.b_skip_after_selecion_end = false;
//				});
//				Handsontable.hooks.add('beforeColumnResize', () => doComfirmControlProp());
//				Handsontable.hooks.add('beforeSetRangeStartOnly', () => doComfirmControlProp());
				//$('th').bind('mouseenter', ()=>alert(1));
		}
}

export default Main;