import {SET_FORM_ID, GET_FORM_DETAIL, SET_FORM_DETAIL_FLOW_ID, SET_FORM_DETAIL_IS_STOP, GET_FORMS_OF_APP, GET_COLUMNS_OF_FORM, SET_FORM_DETAIL_DATA, SET_FORM_FIELD_MAPS, SET_ACTIVE_KEY, SET_IMPORT_EXCEL} from '../actions/form';

const importExcel = (state, action) => {
	if(state === undefined){
		state = false;
	}
	
	switch(action.type){
		case SET_IMPORT_EXCEL:
			return action.importExcel;
		default:
			return state;
	}	
}

const activeKey = (state, action) => {
	if(state === undefined){
		state = '1';
	}
	
	switch(action.type){
		case SET_ACTIVE_KEY:
			return action.activeKey || '1';
		default:
			return state;
	}
}

const formId = (state, action) => {
	if(state === undefined) {
		state = ''
	}

	switch(action.type) {
		case SET_FORM_ID:
			return action.formId || ''
		default:
			return state
	}
}

const formDetail = (state, action) => {
    if (state === undefined) {
        state = {}
    }
    
    switch(action.type){
    		case GET_FORM_DETAIL:
    				return {...state, ...action.form};
    		case SET_FORM_DETAIL_FLOW_ID:
    				return {...state, flowId: action.flowId, flowType: action.flowType};
    		case SET_FORM_DETAIL_IS_STOP:
    				return {...state, isStop: action.isStop};
    		case SET_FORM_DETAIL_DATA:
    				return {...state, formData: action.form};
    		case SET_FORM_FIELD_MAPS:	
    				return {...state, formData: {...state.formData}, fieldMaps: action.fieldMaps}			
    		default:
    				return state;
    }
}

const formsOfApp = (state, action) => {
		if(state === undefined){
				state = [];
		}
		
		switch(action.type){
				case GET_FORMS_OF_APP:
						return {...state, ...action.forms};
				default:
						return state;
		}
}

const columnsOfForm = (state, action) => {
		if(state === undefined){
				state = [];
		}
		
		switch(action.type){
				case GET_COLUMNS_OF_FORM:
						return {...state, ...action.columns};
				default:
						return state;
		}
}

const AllReducers = {
	 formId, formDetail, formsOfApp, columnsOfForm, activeKey, importExcel
}

export default AllReducers;