import React, { Component } from 'react';
import Main from './Main';
import './App.css';
import { connect } from 'react-redux';
import {setFormDetailFlowId, setFormDetailIsStop, saveForm, setFormDetailData, setActiveKey, setImportExcel} from './actions/form';

const mapStateToProps = (state, ownProps) => {
		return {
				formId: state.formId,
				formDetail: state.formDetail,
				formsOfApp : state.formsOfApp,
				columnsOfForm: state.columnsOfForm,
				activeKey: state.activeKey,
				importExcel: state.importExcel
		}
}

const mapDispatchToProps = (dispatch) => {
	return {
			setFlowId: (flowId, flowType) => {
					dispatch(setFormDetailFlowId(flowId, flowType))
			},
			setIsStop: (isStop) => {
					dispatch(setFormDetailIsStop(isStop))
			},
			saveForm: (formId, isStop, Workbook, callback) => {
					dispatch(saveForm(formId, isStop, Workbook, callback));
			},
			setFormDetailData: (form) => {
					dispatch(setFormDetailData(form));
			},
			setActiveKey: (activeKey) => {
				dispatch(setActiveKey(activeKey));
			},
			setImportExcel: (importExcel) => {
				dispatch(setImportExcel(importExcel));
			}
	}
}

class App extends Component {
  render() {
    return (
				<Main {...this.props}/>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);