import { message } from 'antd';
import $ from 'jquery';
const Message = message;

export const SET_FORM_ID = "SET_FORM_ID";
export const GET_FORM_DETAIL = "GET_FORM_DETAIL";
export const SET_FORM_DETAIL_FLOW_ID = "SET_FORM_DETAIL_FLOW_ID";
export const SET_FORM_DETAIL_IS_STOP = "SET_FORM_DETAIL_IS_STOP";
export const GET_FORMS_OF_APP = "GET_FORMS_OF_APP";
export const GET_COLUMNS_OF_FORM = "GET_COLUMNS_OF_FORM";
export const SET_FORM_DETAIL_DATA = "SET_FORM_DETAIL_DATA";
export const SET_FORM_FIELD_MAPS = 'SET_FORM_FIELD_MAPS';
export const SET_ACTIVE_KEY = 'SET_ACTIVE_KEY';
export const SET_IMPORT_EXCEL = 'SET_IMPORT_EXCEL';

export const setImportExcel = (importExcel) => {
	return {
		type: SET_IMPORT_EXCEL,
		importExcel 
	}	
}

export const setActiveKey = (activeKey) => {
	return {
		type: SET_ACTIVE_KEY,
		activeKey 
	}
}

export const SetFormId = (formId) => {
		return {
				type: SET_FORM_ID,
				formId
		}
}
//设置表单信息
export const setFormDetail = (form) => {
    return {
        type: GET_FORM_DETAIL,
        form
    }
}
//设置表单字段信息
export const setFormDetailFieldMaps = (fieldMaps) => {
    return {
        type: SET_FORM_FIELD_MAPS,
        fieldMaps
    }
}
export const setFormDetailFlowId = (flowId, flowType) => {
		return {
        type: SET_FORM_DETAIL_FLOW_ID,
        flowId,
        flowType
    }
}

export const setFormDetailIsStop = (isStop) => {
		return {
        type: SET_FORM_DETAIL_IS_STOP,
        isStop
    }
}

//设置应用所有表单
export const setFormsOfApp = (forms) => {
			return {
					type: GET_FORMS_OF_APP,
					forms	
			}
}

//设置表单字段信息
export const setColumnsOfForm = (columns) => {
		return {
				type: GET_COLUMNS_OF_FORM,
				columns	
		}
}

export const fetchForm = () => {
    return (dispatch, getState) => {
    		let {formId} = getState();
    		if(parseInt(formId) > 0) {
            $.ajax({
                method: "get",
                url: "/general/appbuilder/web/appdesign/appform/fetch",
                data: {formId},
                async: false
            }).done(function (response) {
                const {status, data, items, forms} = response
                if (status != 'ok') {
                    Message.error(response.message)
                } else {
                		document.title = data.name;
                		if(data.formData && data.formData.cellSettings){
                			let {cellSettings, cellMetas, cellClass, images, charts, cellStyles, fieldMaps} = data.formData;
                			data.formData = [{index: "1", name: '工作表1', cellSettings, cellMetas, cellClass, images, charts, cellStyles, fieldMaps}];
                		}
                		dispatch(setFormDetail(data));
                		dispatch(setColumnsOfForm(items));
                		dispatch(setFormsOfApp(forms));
                }
            });
        }
    }
}

export const fetchForms = () => {
		return (dispatch, getState) => {
				let {formId} = getState();
    		if(parseInt(formId) > 0) {
            $.ajax({
                method: "get",
                url: "/general/appbuilder/web/appdesign/appform/forms",
                data: {formId},
                async: false
            }).done(function (response) {
                const {status, data} = response
                if (status != 'ok') {
                    Message.error(response.message)
                } else {
                		dispatch(setFormsOfApp(data));                		
                }
            });
        }
		};
}

export const fetchColumnsOfForm = (formId) => {
		return (dispatch, getState) => {
				if(parseInt(formId) > 0) {
            $.ajax({
                method: "get",
                url: "/general/appbuilder/web/appdesign/appform/items",
                data: {formId, with_id: true},
                async: false
            }).done(function (response) {
                const {status, data} = response
                if (status != 'ok') {
                    Message.error(response.message)
                } else {
                		dispatch(setColumnsOfForm(data));
                }
            });
        }
		}
}

export const saveForm = (formId, isStop, Workbook, callback) => {
    return (dispatch, getState) => {
        $.post('/general/appbuilder/web/appdesign/appform/save', {
            formId, isStop, data: Workbook
        }, (response) => {
            const {status, message} = response

            if (status == "ok") {
                const text = "保存成功！"
                Message.success(text);
                if(response.fieldMaps){
                	Workbook.map((sheet, idx) => {
                		sheet.fieldMaps = response.fieldMaps[idx];
                		sheet.sid = response.sid[idx]
                	});
                }
//                let set_sheets = [];
//                Workbook.map(sheet => set_sheets.push({...sheet}));
                dispatch(setFormDetailData(Workbook));
                dispatch(setColumnsOfForm(response.items));							               
            } else if (status == "error") {
                Message.error(message)
            } else {
                Message.error("保存遇到问题,请稍后再试。")
            }
            callback && callback(); 
        })    	
    }
}

//更新表单信息
export const setFormDetailData = (form) => {
    return {
        type: SET_FORM_DETAIL_DATA,
        form
    }
}